.image {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-gap: 10px;
}
.board {
  position: fixed;
  left: 1100px;
  top: 40px;
  background-color: green;
  color: white;
  opacity: 0.8;
  width: 130px;
  border-radius: 10%;
}
.app {
  background-color: rgb(218, 199, 199);
}
.h1 {
  text-align: center;
}
